.text-field {
  width: 100%;
  min-height: 100px;
  border: 1px solid #ccc;
  outline: none;
  padding: 10px;
  font-size: 18px;
}

.selected {
  color: red;
}

.letter {
  cursor: pointer;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
  display: inline-block;
}

.selection-rect {
  pointer-events: none;
  z-index: 9999;
  border: 1px dashed #00f;
}

